import { useCallback, useEffect } from "react"
import { generatePath, useNavigate } from "react-router-dom"
import useLocation from "react-use/lib/useLocation"
import { BriefCompleted, BriefPageHeader } from "@rp/react/web/brief"
import { CardGradientBackground, PageWrapperStyled } from "@rp/react/web/common/base-components"
import { MaxWidthContainer, VerticalScrollStepper } from "@rp/react/web/common/composite-components"
import { HtmlPageHead } from "@rp/react/web/common/header-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { BRIEF_ROUTE, WATCHLIST_ID_ROUTE } from "@rp/react/web/router"

type BriefCreateOrEditSuccessProps = {
  isEdit?: boolean
}

const BriefCreateOrEditSuccess = ({ isEdit = false }: BriefCreateOrEditSuccessProps) => {
  const navigate = useNavigate()
  const { isMobile } = useDeviceInfo()
  const location = useLocation()
  const initialWatchlistId = location?.state?.usr?.initialWatchlistId

  useEffect(() => {
    const className = isMobile ? "freezeScroll" : "scrollYlScroll"
    window.document.body.classList.add(className)
    return () => {
      window.document.body.classList.remove(className)
    }
  }, [isMobile])

  const numSteps = isEdit || initialWatchlistId ? 2 : 3
  const pageHeaderTitle = isEdit ? "Edit Brief" : "New Brief"
  const stepperTitle = isEdit ? "Edit a Brief" : "Schedule a Brief"

  const newRoute = initialWatchlistId
    ? generatePath(WATCHLIST_ID_ROUTE, { id: initialWatchlistId })
    : BRIEF_ROUTE
  const goBack = useCallback(() => navigate(newRoute, { replace: true }), [navigate, newRoute])

  return (
    <PageWrapperStyled>
      <HtmlPageHead title={"Brief"} />
      <BriefPageHeader title={pageHeaderTitle} onBackButtonClick={goBack} />
      <CardGradientBackground />
      <MaxWidthContainer style={{ overflow: "visible" }}>
        <VerticalScrollStepper
          disableAutoScroll
          completeButtonText={"Go Back"}
          stepLabel={`Step ${numSteps}/${numSteps}`}
          title={stepperTitle}
          steps={numSteps}
          activeStep={numSteps}
          handleComplete={goBack}
        >
          <BriefCompleted isEdit={isEdit} />
        </VerticalScrollStepper>
      </MaxWidthContainer>
    </PageWrapperStyled>
  )
}

export { BriefCreateOrEditSuccess }
